@import '../../common/utils/vars.scss';
@import '../../common/base/fonts.scss';

@media screen and (min-width: 768px) {
    .container {
        background-image: url(../../images/RegistrationPage/Tablet/ellipse-tablet-purple.png),
            url(../../images/RegistrationPage/Tablet/ellipse-tablet-pink.png);

        background-repeat: no-repeat, no-repeat;
        background-position: bottom left, top right;
        background-color:var(--bg-registration-color);
    }

    .logo {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 60px;
    }

    .image {
        margin-bottom: 32px;
        width: 274px;
        height: 250px;
    }

    .title {
        font-family: 'Poppins';
        font-weight: 400;
        font-size: 30px;
        line-height: 1.5;
    }

    .form {
        padding: 52px 91px 52px 107px;
    }
}

/*Desktop*/
@media screen and (min-width: 1280px) {
    .container {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100vh;
        width: 100vw;
        margin: 0;
        padding: 0;
        overflow: hidden;
    }

    .logo {
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 153px 31px 77.5px 66px;
    }

    .image {
        margin-bottom: 32px;
        width: 452px;
        height: 413px;
    }

    .form {
        min-height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 52px 91px 52px 107px;
        background: var(--bg-main-color);
        backdrop-filter: blur(25px);
    }
}